<template>
  <div id="app">
    <div class="bg">
      <img v-lazy="'/img/bg1.jpg'">
      <img v-lazy="'/img/bg2.jpg'">
      <img v-lazy="'/img/bg3.jpg'">
      <img v-lazy="'/img/bg4.jpg'">
      <img v-lazy="'/img/bg5.jpg'">
      <img v-lazy="'/img/bg6.jpg'">
    </div>
    <div
      v-if="showMasterVideo"
      class="v0"
    >
      <video
        id="v0"
        ref="v1"
        :muted="videoOption.muted"
        :autoplay="videoOption.autoplay"
        :controls="videoOption.controls"
        :preload="videoOption.preload"
        :loop="videoOption.loop"
        :playsinline="videoOption.playsinline"
        :webkit-playsinline="videoOption.playsinline"
        :poster="masterVideo.img"
        :src="masterVideo.src"
        :x-webkit-airplay="videoOption.xWebkitAirplay"
        :x5-video-player-type="videoOption.x5VideoPlayerType"
        :x5-video-player-fullscreen="videoOption.x5VideoPlayerFullscreen"
      />
    </div>
    <div class="container">
      <div class="banner">
        <van-swipe
          ref="banner"
          :show-indicators="false"
          :touchable="false"
          @change="bannerChange"
        >
          <van-swipe-item
            v-for="(items, i) in banner.data"
            :key="i"
          >
            <div class="bannerImg">
              <img v-lazy="`/img/banner${i}.png`">
            </div>
            <div class="slideBox">
              <template v-for="(item, index) in items">
                <div
                  :key="index"
                  class="pbox"
                  @click="popup(item)"
                />
              </template>
            </div>
          </van-swipe-item>
        </van-swipe>
      </div>
      <div class="bbox b1">
        <div @click="replace('banner')" />
      </div>
      <div class="stars">
        <van-swipe
          ref="stars"
          :show-indicators="false"
          :touchable="false"
          @change="starsChange"
        >
          <van-swipe-item
            v-for="(items, i) in stars.data"
            :key="i"
          >
            <div
              v-if="i<2"
              class="slideBox"
            >
              <template v-for="(item, index) in items">
                <div
                  :key="index"
                  class="pbox"
                  @click="fullPlay(item)"
                >
                  <img
                    v-lazy="`/img/${i+1}-${index+1}.png`"
                    class=""
                  >
                </div>
              </template>
            </div>
          </van-swipe-item>
        </van-swipe>
      </div>
      <div class="bbox b2">
        <div @click="replace('stars')" />
      </div>
      <div class="vbox v2">
        <div
          class="video"
          @click="fullPlay(masterStar)"
        />
      </div>
      <template v-for="(item, index) in videoGroup">
        <div
          v-if="index!==1"
          :key="index"
          :class="'vbox v' + (index + 1)"
        >
          <video
            v-show="index === 1 || index === 3"
            :id="'v' + (index + 1)"
            :muted="index === 0 ? item.muted : videoOption.muted"
            :autoplay="index === 0 ? 'true' : videoOption.autoplay"
            :controls="videoOption.controls"
            :preload="videoOption.preload"
            :loop="videoOption.loop"
            :playsinline="videoOption.playsinline"
            :webkit-playsinline="videoOption.playsinline"
            :poster="item.img"
            :src="item.src"
            :x-webkit-airplay="videoOption.xWebkitAirplay"
            :x5-video-player-type="videoOption.x5VideoPlayerType"
            :x5-video-player-fullscreen="
              index === 0 ? 'false' : videoOption.x5VideoPlayerFullscreen
            "
          />
        </div>
      </template>
      <van-popup
        v-model="popupShow"
        lock-scroll
      >
        <div class="bannerPopup">
          <div class="close">
            <div @click="popupShow=false" />
          </div>
          <div class="bannerChildren">
            <van-swipe>
              <van-swipe-item
                v-for="(item, i) in bannerItem.children"
                :key="i"
              >
                <img v-lazy="`/img/landscape/${item.src}.jpg`">
              </van-swipe-item>
            </van-swipe>
          </div>
          <img
            :src="`/img/title/${bannerItem.titleId}.png`"
            class="title"
          >
          <div class="description">
            {{ bannerItem.description }}
          </div>
        </div>
      </van-popup>
    </div>
  </div>
</template>

<script>
import axios from 'axios'
import banner from '@/api/banner'
import stars from '@/api/stars'

export default {
  name: 'App',
  data() {
    return {
      popupShow: false,
      showMasterVideo: false,
      banner: {
        index: 0,
        data: banner.body
      },
      bannerItem: {
        titleId: '',
        description: '',
        children: []
      },
      stars: {
        index: 0,
        data: stars.body
      },
      masterStar: {
        name: '周笔畅',
        src:
          'https://7.jstyle.cn/3/2019/12/27/0888de0d752f654ec835c419f809cd33.mp4'
      },
      videoOption: {
        muted: false, //静音
        autoplay: false, //自动播放 微信内嵌浏览器不支持，ios qq浏览器不支持
        controls: true, //显示控件
        preload: 'none', //是否预加载数据 auto:页面加载后载入整个数据, meta:页面加载后载入元数据, none:不载入视频
        loop: true, //循环
        playsinline: true, //小窗播放   苹果支持
        xWebkitAirplay: 'allow', //通过AirPlay可以把当前的视频投放到支持此技术的其他设备上。苹果支持，安卓不支持(全屏播放),
        x5VideoPlayerType: 'h5', //声明启用同层H5播放器
        x5VideoPlayerFullscreen: true //全屏模式
      },
      masterVideo: {
        src:
          'https://7.jstyle.cn/3/2019/12/27/de923adf5f56f6c367ec0c6b44d8ad68.mp4',
        img: ''
      },
      videoGroup: [
        {
          src: 'http://demo-videos.qnsdk.com/movies/qiniu.mp4',
          img: '',
          muted: true
        },
        {
          src:
            'https://7.jstyle.cn/3/2019/12/27/0888de0d752f654ec835c419f809cd33.mp4',
          img: ''
        },
        {
          src: 'http://demo-videos.qnsdk.com/movies/qiniu.mp4',
          img: ''
        },
        {
          src:
            'https://7.jstyle.cn/3/2019/12/27/6e7807b7b8558a0d5c9cba5aa35a74a0.mp4',
          img: ''
        }
      ],
      isMQQBrowser: navigator.appVersion.indexOf('MQQBrowser') !== -1
    }
  },
  mounted() {
    // const video = document.getElementById('v1')

    //safari、chrome浏览器自动播放
    //遗留问题：qq浏览器会随机全屏播放
    // if (!this.isMQQBrowser) {
    //   setTimeout(function() {
    //     video.play()
    //   }, 1000)
    // }

    //解决微信浏览器自动播放问题
    // document.addEventListener(
    //   'WeixinJSBridgeReady',
    //   () => {
    //     video.play()
    //   },
    //   false
    // )
    // document.addEventListener(
    //   'WeixinJSBridgeReady',
    //   () => {
    //
    //   },
    //   false
    // )

    //配置分享信息
    axios
      .get('http://mapi.jstyle.cn/wechat/share/?api.init&project=4', {
        params: {
          url: 'http://172.20.10.2:8080'
        }
      })
      .then(function(res) {
        const { appId, timestamp, nonceStr, signature } = res.data.data
        wx.config({
          debug: false, // 开启调试模式。
          appId, // 必填，公众号的唯一标识
          timestamp, // 必填，生成签名的时间戳
          nonceStr, // 必填，生成签名的随机串
          signature, // 必填，签名
          jsApiList: ['updateAppMessageShareData'] // 必填，需要使用的JS接口列表
        })
        wx.ready(function() {
          //需在用户可能点击分享按钮前就先调用
          wx.updateAppMessageShareData({
            title: '111', // 分享标题
            desc: '222', // 分享描述
            link: '', // 分享链接，该链接域名或路径必须与当前页面对应的公众号JS安全域名一致
            imgUrl: '', // 分享图标
            success: function() {
              // 设置成功
              console.log('分享设置成功')
            }
          })
        })
      })

    //解决chrome浏览器（pc端）退出全屏无法重置视频的问题
    const that = this
    document.addEventListener('webkitfullscreenchange', function(e) {
      if (document.webkitFullscreenElement) {
        console.log('进入全屏')
      } else {
        const video = document.getElementById('v0')
        that.resetVideo(video)
        console.log('退出全屏')
      }
    })
  },
  methods: {
    replace(name) {
      // this.fullPlay("v0")
      this.showMasterVideo = false
      this[name].index = this[name].index + 1
      this.$refs[name].swipeTo(this[name].index)
    },
    bannerChange(index) {
      this.banner.index = this.banner.data.length === index ? 0 : index
    },
    popup(item) {
      this.popupShow = true
      this.bannerItem = item
    },
    starsChange(index) {
      this.stars.index = this.stars.data.length === index ? 0 : index
    },
    fullPlay(videoData) {
      if (!videoData.src) return
      this.showMasterVideo = true
      this.masterVideo = videoData
      setTimeout(() => {
        const video = document.getElementById('v0')
        //监听全屏退出事件，退出时关闭停止视频播放
        video.addEventListener('webkitendfullscreen', () => {
          this.resetVideo(video)
          this.showMasterVideo = false
          video.removeEventListener('webkitendfullscreen', () => {})
        })
        //解决不同浏览器全屏播放的兼容性问题
        if (video.requestFullscreen) {
          video.requestFullscreen()
        } else if (video.webkitRequestFullscreen) {
          video.webkitRequestFullscreen()
        } else if (video.mozRequestFullScreen) {
          video.mozRequestFullScreen()
        } else if (video.msRequestFullscreen) {
          video.msRequestFullscreen()
        } else {
          video.webkitEnterFullscreen()
        }

        video.play()
      }, 100)
    },
    resetVideo(video) {
      video.currentTime = 0
      video.pause()
    }
  }
}
</script>

